import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import config from './config.json'

@inject('ethereumStore')
@observer
export default class _Network extends Component {
    constructor(props) {
        super(props);
    }
    setProvider = (provider) => {
        this.props.ethereumStore.setProvider(provider)
        this.forceUpdate()
    }
    
    render() {
        return (
            <div className="form-group" >
                Ethereum Network
                <div className="radio" >
                    <label>
                        <input type="radio" name="network"
                            checked={this.props.ethereumStore.networkId === 1}
                            onChange={() => {
                                this.setProvider(1)
                            }} />
                        主網路
                    </label>
                </div>
                <div className="radio">
                    <label>
                        <input type="radio" name="network"
                            checked={this.props.ethereumStore.networkId === 3}
                            onChange={() => {
                                this.setProvider(3)
                            }} />
                        測試網路
                    </label>
                </div>
            </div>
        )
    }
}
export { _Network };